// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import $ from 'jquery'
import FastClick from 'fastclick'

import AlloyFinger from 'alloyfinger'
import AlloyFingerPlugin from 'alloyfinger/vue/alloy_finger.vue'
Vue.use(AlloyFingerPlugin, {
  AlloyFinger
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {App},
  template: '<App/>',
  data: {
    eventHub: new Vue(),
    article: {},
    head: '',
    width: window.innerWidth,
    height: window.innerHeight,
    dpr: window.devicePixelRatio,
    records: [
      {name: 'home', pos: 0},
      {name: 'sign', pos: 0},
      {name: 'products', pos: 0},
      {name: 'product', pos: 0},
      {name: 'news', pos: 0},
      {name: 'support', pos: 0},
      {name: 'partner', pos: 0},
      {name: 'introduction', pos: 0},
      {name: 'user', pos: 0},
      {name: 'article', pos: 0}
    ],
    visit: 1,
    visitMode: 0, // 0 初次访问，1 历史访问
    // Belongs to Products.vue
    detailTitle: '',
    detailStatus: false,
    mode: 0,
    // Height of Carousel
    carousel: 0.525 * window.innerHeight
  },
  methods: {
    setRecord: function (x, y) {
      for (var i = 0; i < this.$root.records.length; i++) {
        var temp = this.$root.records[i]
        if (temp.name === x) {
          temp.pos = y
          break
        }
      }
    },
    getRecord: function (x) {
      for (var i = 0; i < this.$root.records.length; i++) {
        var temp = this.$root.records[i]
        if (temp.name === x) {
          return temp.pos
        }
      }
    },
    modalHeight: function (i) {
      var height
      switch (i) {
        case 0:
        case 1:
          height = 265
          break
      }
      var str = 'height: ' + height + 'px'
      return str
    },
    getCurrentDate: function (obj) {
      var d = new Date()
      obj.year = d.getFullYear()
      obj.month = d.getMonth() + 1
      obj.day = d.getDate()
    }
  },
  computed: {
    adapt: function () {
      if (window.screen.width < 768) {
        return false
      } else {
        return true
      }
    }
  },
  watch: {
    '$route': function (to, from) {
      if (this.$root.visit === 1 && to.name.substring(0, to.name.length - 1) === from.name) {
        this.$root.visitMode = 1
      } else {
        $('#app').scrollTop(0)
        this.$root.visit = 1
        this.$root.visitMode = 0
      }
    }
  }
})

FastClick.attach(document.body)



