import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'home',
      component: resolve => require(['@/components/Center'], resolve)
    },
    {
      path: '/sign',
      name: 'sign',
      component: resolve => require(['@/components/Sign'], resolve)
    },
    {
      path: '/products',
      name: 'products',
      component: resolve => require(['@/components/Products'], resolve)
    },
    {
      path: '/product',
      name: 'product',
      component: resolve => require(['@/components/Product'], resolve)
    },
    {
      path: '/news',
      name: 'news',
      component: resolve => require(['@/components/News'], resolve)
    },
    {
      path: '/support',
      name: 'support',
      component: resolve => require(['@/components/Support'], resolve)
    },
    {
      path: '/partner',
      name: 'partner',
      component: resolve => require(['@/components/Partner'], resolve)
    },
    {
      path: '/introduction',
      name: 'introduction',
      component: resolve => require(['@/components/Intro'], resolve)
    },
    {
      path: '/user',
      name: 'user',
      component: resolve => require(['@/components/User'], resolve),
      children: [
        {
          path: '/user/reimbursement',
          name: 'Reimbursement',
          component: resolve => require(['@/components/Reimbursement'], resolve),
          children: [
            {
              path: '/user/reimbursement/application',
              name: 'ReApplication',
              component: resolve => require(['@/components/ReApplication'], resolve)
            },
            {
              path: '/user/reimbursement/table',
              name: 'ReForm',
              component: resolve => require(['@/components/ReForm'], resolve)
            }
          ]
        },
        {
          path: '/user/accounting',
          name: 'Accounting',
          component: resolve => require(['@/components/Accounting'], resolve)
        },
        {
          path: '/user/news',
          name: 'NewsManage',
          component: resolve => require(['@/components/NewsManage'], resolve),
          children: [
            {
              path: '/user/news/upload',
              name: 'NewsUpload',
              component: resolve => require(['@/components/NewsUpload'], resolve)
            }
          ]
        },
        {
          path: '/user/products',
          name: 'ProductsManage',
          component: resolve => require(['@/components/ProductsManage'], resolve)
        },
        {
          path: '/user/setting',
          name: 'Setting',
          component: resolve => require(['@/components/Setting'], resolve)
        },
        {
          path: '/user/authority',
          name: 'Authority',
          component: resolve => require(['@/components/Authority'], resolve)
        }
      ]
    },
    {
      path: '/article',
      name: 'art',
      component: resolve => require(['@/components/Art'], resolve)
    }
  ]
})



