<template>
  <div id="app" @touchstart="hideNav" @touchmove="hideNav" @touchend="hideNav">
    <transition name="contentMode">
      <div v-if="contentMode" class="nav-bar" :class="{'active': navStatus, 'list': list}" @mouseover="nav" @mouseout="nav0">
        <div class="grey" :class="{'active': navStatus}"></div>
        <div class="real" @click="hideList1">
          <div class="nav-top">
            <div class="name left pointer" @click="home">
              <p>普信天宇</p>
            </div>
            <div v-if="this.$root.adapt && !signStatus" class="func right pointer">
              <p @click="sign">登录</p>
            </div>
            <div v-if="signStatus" id="sign-user" class="pointer right" @click="user" :style="userHead"></div>
          </div>
          <div v-if="this.$root.adapt" class="nav-bottom">
            <div class="nav-item-bar">
              <div v-for="(item, index) in navList" v-bind:key="index" class="nav-item left" @mouseover="showList(index)" @mouseout="hideList">
                <div class="pointer" @click="fun(index)">
                  <p>{{item}}</p>
                  <div class="input-bottom">
                    <transition name="input">
                      <div v-if="focus(index)" class="new"></div>
                    </transition>
                  </div>
                </div>
                <div v-if="index === 0" class="products-list">
                </div>
              </div>
            </div>
            <div class="nav-item all pointer" @mouseover="showList" @mouseout="hideList">
              <p>全部导航</p>
            </div>
          </div>
          <div class="nav-button right" @click="showList0"></div>
          <div v-if="signStatus && !this.$root.adapt" id="mobile-sign-user" class="right" @click="user">
            <div :style="userHead"></div>
          </div>
        </div>
      </div>
    </transition>
    <div v-if="top" class="back-to-top pointer" @click="backToTop">
      <div></div>
    </div>
    <transition name="list">
      <div v-show="list" class="navList">
        <div v-if="!this.$root.adapt" class="navList-wrapper" @touchmove="moveNavList">
          <div v-if="!(index === 5 && signStatus)" v-for="(item, index) in func" v-bind:key="index" class="navList-item" @click="fun(index)">
            <p>{{item}}</p>
          </div>
        </div>
      </div>
    </transition>
    <transition name="grey">
      <div v-if="!this.$root.adapt && list" class="grey-cover" @click="showList0"></div>
    </transition>
    <transition name="grey">
      <div v-if="prompt" class="grey-prompt" @click="cancelPrompt"></div>
    </transition>
    <div class="prompt-bar">
      <transition name="prompt">
        <div class="prompt-modal" v-if="isShow">
          <p>{{promptText}}</p>
        </div>
      </transition>
    </div>
    <router-view/>
  </div>
</template>

<script>
import $ from 'jquery'
import {checkCookie, clearCookie} from '../resource/js/cache.js'
export default {
  name: 'App',
  data () {
    return {
      navList: [
        '产品与解决方案',
        '新闻中心',
        '技术支持',
        '合作伙伴',
        '关于我们'
      ],
      func: [
        '产品与解决方案',
        '新闻中心',
        '技术支持',
        '合作伙伴',
        '关于我们',
        '登录 / 注册'
      ],
      navStatus: false,
      signStatus: false,
      list: false,
      navIndex: -1,
      top: false,
      prompt: false,
      isShow: false,
      promptText: '',
      posY: 0,
      contentMode: true,
      timeout: ''
    }
  },
  methods: {
    sign: function () {
      this.$root.visit = 0
      this.$router.push({path: '/sign'})
    },
    products: function () {
      this.$root.visit = 0
      this.$router.push({path: '/products'})
    },
    news: function () {
      this.$root.visit = 0
      this.$router.push({path: '/news'})
    },
    support: function () {
      this.$root.visit = 0
      this.$router.push({path: '/support'})
    },
    partner: function () {
      this.$root.visit = 0
      this.$router.push({path: '/partner'})
    },
    introduction: function () {
      this.$root.visit = 0
      this.$router.push({path: '/introduction'})
    },
    user: function () {
      this.$root.visit = 0
      this.$router.push({path: '/user'})
      if (!this.$root.adapt) {
        $('.grey').css('opacity', 0)
        this.top = false
      }
    },
    home: function () {
      this.$root.visit = 0
      this.$router.push({path: '/'})
    },
    nav: function () {
      if (this.$root.adapt && this.$root.dpr === 1) {
        this.navStatus = true
      }
    },
    nav0: function () {
      if (this.$root.adapt && this.$root.dpr === 1) {
        this.navStatus = false
      }
    },
    showList: function (i) {
      if (this.$root.dpr === 1) {
        this.navIndex = i
      }
    },
    showList0: function () {
      if (this.list) {
        $('.nav-bar').css('border-bottom', 'none')
        this.nav0()
      } else {
        $('.nav-bar').css('border-bottom', '1px solid hsla(0,0%,100%,.15)')
        this.nav()
      }
      this.list = !this.list
      $('.navList-wrapper').scrollTop(0)
    },
    hideList: function () {
      if (this.$root.dpr === 1) {
        this.list = false
        this.navIndex = -1
      }
    },
    hideList0: function () {
      this.hideList()
      this.nav0()
    },
    hideList1: function () {
      if (!this.$root.adapt) {
        if (event.target.className !== 'nav-button right' && this.list) {
          this.showList0()
        }
      }
    },
    seeList: function () {
      this.navIndex = this.temp
      this.nav()
    },
    fun: function (i) {
      if (this.$root.adapt) {
        this.navIndex = -1
      } else {
        this.showList0()
      }
      switch (i) {
        case 0:
          this.products()
          break
        case 1:
          this.news()
          break
        case 2:
          this.support()
          break
        case 3:
          this.partner()
          break
        case 4:
          this.introduction()
          break
        case 5:
          this.sign()
          break
      }
    },
    focus: function (i) {
      if (i === this.navIndex) {
        return true
      } else {
        return false
      }
    },
    clear: function () {
      clearCookie()
      this.signStatus = false
    },
    backToTop: function () {
      $('#app').animate({scrollTop: 0}, 400)
    },
    cancelPrompt: function () {
      if (!this.isShow) {
        this.prompt = false
        this.$root.eventHub.$emit('cancelPrompt')
      }
    },
    hideNav: function (ev) {
      switch (ev.type) {
        case 'touchstart':
          this.posY = ev.targetTouches[0].pageY
          break
        case 'touchmove':
          var height = this.$root.carousel + 50
          var temp = ev.targetTouches[0].pageY - this.posY
          console.log(temp)
          if ($('#app').scrollTop() === 0 && temp > 0) {
            ev.preventDefault()
          }
          var tag = $('#app').scrollTop() > height
          if (tag && temp < -50) {
            this.contentMode = false
          } else if (!tag || temp > 50) {
            this.contentMode = true
          }
          break
      }
    },
    renderNav: function () {
      if (!this.$root.adapt) {
        var temp = this.$root.carousel
        $('.navList').css('height', temp)
        var padding
        if (temp < 300) {
          padding = (temp - 150) / 8
          $('.navList-item').css('padding', padding + 'px 0')
        } else {
          padding = (temp - 175) / 10
          $('.navList-item').css('padding', padding + 'px 0')
        }
      }
    },
    moveNavList: function (ev) {
      ev.stopPropagation()
    }
  },
  computed: {
    userHead: function () {
      if (this.$root.head === '') {
        // return 'background: url(\'resource/assets/user-circle3.jpg\')'
        return 'background: url(\'resource/assets/user.png\')'
      } else {
        return 'background: url(\'' + this.$root.head + '\')'
      }
    }
  },
  watch: {
    signStatus: 'renderNav'
  },
  mounted () {
    var self = this
    // 验证登录状态
    this.signStatus = checkCookie(2, '')
    this.renderNav()
    function realFunc (ev) {
      if (self.list) {
        ev.preventDefault()
      }
      var temp = $('#main').height()
      var top = document.getElementById('app').scrollTop
      var path = self.$route.path.split('/')[1]
      if (!self.$root.adapt && path !== 'user') {
        var opacity = (top / (temp - 50)).toFixed(1)
        opacity = opacity > 1 ? 1 : opacity
        $('.grey').css('opacity', opacity)
      }
      if (self.$root.adapt) {
        if (top >= temp - 140) {
          if (path !== 'user') {
            self.top = true
          }
          if (self.$route.name === 'home') {
            self.$root.eventHub.$emit('showSolution')
          }
        } else {
          if (path !== 'user') {
            self.top = false
          }
        }
      } else {
        if (self.$route.name === 'home') {
          if (top >= temp - 120) {
            self.$root.eventHub.$emit('showSolution')
          }
        }
      }
    }
    // function throttling (func, wait, mustRun) {
    //   var timeout
    //   var startTime = new Date()
    //   return function () {
    //     var context = this
    //     var args = arguments
    //     var curTime = new Date()
    //     clearTimeout(timeout)
    //     // 如果达到了规定的触发时间间隔，触发 handler
    //     if (curTime - startTime >= mustRun) {
    //       func.apply(context, args)
    //       startTime = curTime
    //       // 没达到触发间隔，重新设定定时器
    //     } else {
    //       timeout = setTimeout(func, wait)
    //     }
    //   }
    // }
    $('#app').scroll(realFunc)
    this.$root.eventHub.$on('sign', function () {
      self.signStatus = checkCookie(2, '')
      if (!self.$root.adapt) {
        $('.grey').css('opacity', 0)
      }
    })
    this.$root.eventHub.$on('hide', function () {
      if (self.list) {
        self.showList0()
      }
    })
    this.$root.eventHub.$on('clear', function () {
      self.clear()
    })
    this.$root.eventHub.$on('prompt', function () {
      self.prompt = !self.prompt
    })
    this.$root.eventHub.$on('showPrompt', function (x) {
      self.prompt = true
      self.promptText = x.text
      self.isShow = true
      setTimeout(function () {
        if (self.isShow) {
          self.prompt = false
          self.isShow = false
          setTimeout(function () {
            self.promptText = ''
          }, 250)
        }
      }, x.duration)
    })
  }
}
</script>

