    }
    .prompt-modal{
      width: auto;
      padding: 12px 18px;
      top: 30px;
      left: calc((100% - 200px) / 2);
    }
    .nav-item .products-list{
      display: none;
    }
    .nav-item:hover .products-list{
      display: inline;
    }
    #main{
      width: 100%;
      height: 540px;
    }
    .products-list{
      height: 440px;
    }
  }

  @media (min-device-width: 768px) and (min-width: 900px) {
    .back-to-top{
      right: 120px;
    }
  }

  @media (min-device-width: 768px) and (min-width: 1140px) {
    .back-to-top{
      right: 160px;
    }
  }

  @media only screen and (min-device-width: 768px) and (-webkit-device-pixel-ratio: 1) {
    .func:hover, .func:active, .nav-item:hover, .nav-item:active{
      color: #00BFFF;
    }
  }

  @media only screen and (min-device-width: 768px) and (min-height: 960px) {
    #main{
      height: 600px;
    }
    .products-list{
      height: 500px;
    }
  }
</style>



